.container-right{
    width: 40%;
    padding-left: 15px;  
    display: flex;
    align-items: center;
    justify-content: center;
}
.App-logo {
    animation: App-logo-spin infinite 20s linear;
    pointer-events: none;
  }
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }